.auch-wrap {
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0; top: 0; right: 0; bottom: 0;
  }

  .auch-modal {
    
    position: absolute;
    left: 50%;
    top: 47%;
    display: block;
    width: 320px;
    height: 500px;
    background-color: #28a3e8;
    transform: translate(-50%, -50%);
    border-radius: 12px;
    border: 1px solid gray;
    box-shadow: 5px 5px 10px rgba(88, 88, 86, 0.5);
  }

  .disable {
    display: none;
  }

  .btn-close {
border: 1px solid rgb(5, 5, 5);
border-radius: 12px;
text-align: center;
display: block;
text-align: center;
margin: 5% 30%;
align-items: center;
text-align: center;
cursor: pointer;
background-color: #ffff00;
  }


